<template>
	<view class="profile-container">
		<!-- È°µÈù¢Ê†áÈ¢ò -->
		<view class="profile-header">
			<text class="profile-title">‰∏™‰∫∫ËµÑÊñô</text>
		</view>
		
		<!-- Áî®Êà∑‰ø°ÊÅØÂå∫Âüü -->
		<view class="user-info-section">
			<!-- Â§¥ÂÉè -->
			<view class="avatar-section" @click="handleAvatarClick">
				<view class="avatar-container">
					<image class="avatar" :src="userInfo.avatarUrl || '/static/default-avatar.png'" mode="aspectFill"></image>
					<view class="avatar-edit-icon">
						<text>üì∑</text>
					</view>
				</view>
				<text class="avatar-tip">ÁÇπÂáªÊõ¥Êç¢Â§¥ÂÉè</text>
			</view>
			
			<!-- ÊòµÁß∞ -->
			<view class="info-item" @click="handleNicknameClick">
				<view class="info-label">
					<text class="label-text">ÊòµÁß∞</text>
				</view>
				<view class="info-content">
					<text class="content-text">{{ userInfo.nickName || userInfo.username || 'Êú™ËÆæÁΩÆ' }}</text>
					<text class="edit-icon">‚úèÔ∏è</text>
				</view>
			</view>
			
			<!-- ÊâãÊú∫Âè∑ -->
			<view class="info-item" @click="handlePhoneClick">
				<view class="info-label">
					<text class="label-text">ÊâãÊú∫Âè∑</text>
				</view>
				<view class="info-content">
					<text class="content-text">{{ userInfo.phone || 'Êú™ÁªëÂÆö' }}</text>
					<text class="edit-icon">‚úèÔ∏è</text>
				</view>
			</view>
			
			<!-- ÁôªÂΩïÊñπÂºè -->
			<view class="info-item">
				<view class="info-label">
					<text class="label-text">ÁôªÂΩïÊñπÂºè</text>
				</view>
				<view class="info-content">
					<text class="content-text">{{ getLoginTypeText() }}</text>
				</view>
			</view>
			
			<!-- Áî®Êà∑ID -->
			<view class="info-item">
				<view class="info-label">
					<text class="label-text">Áî®Êà∑ID</text>
				</view>
				<view class="info-content">
					<text class="content-text">{{ userInfo.userId || '' }}</text>
				</view>
			</view>
		</view>
		
		<!-- ÈÄÄÂá∫ÁôªÂΩïÊåâÈíÆ -->
		<view class="logout-section">
			<button class="logout-btn" @click="handleLogout">ÈÄÄÂá∫ÁôªÂΩï</button>
		</view>
		
		<!-- ÊòµÁß∞ÁºñËæëÂºπÁ™ó -->
		<uni-popup ref="nicknamePopup" type="center" :mask-click="false">
			<view class="edit-modal">
				<view class="modal-header">
					<text class="modal-title">ÁºñËæëÊòµÁß∞</text>
					<text class="close-btn" @click="closeNicknamePopup">√ó</text>
				</view>
				<view class="modal-body">
					<view class="input-group">
						<input class="input-field" v-model="editNickname" placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞" maxlength="20" />
					</view>
					<view class="button-group">
						<button class="cancel-btn" @click="closeNicknamePopup">ÂèñÊ∂à</button>
						<button class="confirm-btn" @click="confirmNickname">Á°ÆÂÆö</button>
					</view>
				</view>
			</view>
		</uni-popup>
		
		<!-- ÊâãÊú∫Âè∑ÁªëÂÆöÂºπÁ™ó -->
		<uni-popup ref="phonePopup" type="center" :mask-click="false">
			<view class="edit-modal">
				<view class="modal-header">
					<text class="modal-title">ÁªëÂÆöÊâãÊú∫Âè∑</text>
					<text class="close-btn" @click="closePhonePopup">√ó</text>
				</view>
				<view class="modal-body">
					<view class="input-group">
						<input class="input-field" v-model="editPhone" placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑" maxlength="11" />
					</view>
					<view class="input-group sms-group">
						<input class="input-field sms-input" v-model="smsCode" placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å" maxlength="6" />
						<button class="send-sms-btn" :disabled="smsCountdown > 0" @click="sendSmsCode">
							{{ smsCountdown > 0 ? `${smsCountdown}s` : 'ÂèëÈÄÅÈ™åËØÅÁ†Å' }}
						</button>
					</view>
					<view class="button-group">
						<button class="cancel-btn" @click="closePhonePopup">ÂèñÊ∂à</button>
						<button class="confirm-btn" @click="confirmPhone">Á°ÆÂÆö</button>
					</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
export default {
	data() {
		return {
			userInfo: {},
			editNickname: '',
			editPhone: '',
			smsCode: '',
			smsCountdown: 0
		};
	},
	
	onShow() {
		this.loadUserInfo();
	},
	
	methods: {
		// Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
		loadUserInfo() {
			const userInfo = uni.getStorageSync('userInfo');
			if (userInfo) {
				this.userInfo = userInfo;
			}
		},
		
		// Ëé∑ÂèñÁôªÂΩïÊñπÂºèÊñáÊú¨
		getLoginTypeText() {
			const typeMap = {
				'wechat': 'ÂæÆ‰ø°ÁôªÂΩï',
				'quick': '‰∏ÄÈîÆÁôªÂΩï',
				'password': 'Ë¥¶Âè∑ÂØÜÁ†Å',
				'sms': 'ÊâãÊú∫È™åËØÅÁ†Å'
			};
			return typeMap[this.userInfo.loginType] || 'Êú™Áü•ÊñπÂºè';
		},
		
		// Â§ÑÁêÜÂ§¥ÂÉèÁÇπÂáª
		handleAvatarClick() {
			// ‰ΩøÁî®ÂæÆ‰ø°Â§¥ÂÉèÈÄâÊã©Âô®
			this.chooseAvatar();
		},
		
		// ÈÄâÊã©Â§¥ÂÉè
		async chooseAvatar() {
			try {
				// Ê£ÄÊü•ÊòØÂê¶ÊîØÊåÅÂ§¥ÂÉèÈÄâÊã©
				if (!uni.chooseImage) {
					uni.showToast({
						title: 'ÂΩìÂâçÁéØÂ¢É‰∏çÊîØÊåÅÂ§¥ÂÉèÈÄâÊã©',
						icon: 'none'
					});
					return;
				}
				
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album', 'camera'],
					success: async (res) => {
						try {
							console.log('ÈÄâÊã©Â§¥ÂÉèÊàêÂäü:', res);
							const tempFilePath = res.tempFilePaths[0];
							
							uni.showLoading({ title: '‰∏ä‰º†Â§¥ÂÉè‰∏≠...' });
							
							// ‰∏ä‰º†Â§¥ÂÉèÂà∞‰∫ëÂ≠òÂÇ®
							const uploadResult = await this.uploadAvatar(tempFilePath);
							
							if (uploadResult) {
								// Êõ¥Êñ∞Áî®Êà∑Â§¥ÂÉè
								this.userInfo.avatarUrl = uploadResult;
								
								// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
								uni.setStorageSync('userInfo', this.userInfo);
								
								// Êõ¥Êñ∞Âà∞‰∫ëÊï∞ÊçÆÂ∫ì
								await this.updateUserInfo({ avatarUrl: this.userInfo.avatarUrl });
								
								uni.showToast({
									title: 'Â§¥ÂÉèÊõ¥Êñ∞ÊàêÂäü',
									icon: 'success'
								});
							}
						} catch (error) {
							console.error('Â§ÑÁêÜÂ§¥ÂÉèÂ§±Ë¥•:', error);
							uni.showToast({
								title: 'Â§¥ÂÉèÂ§ÑÁêÜÂ§±Ë¥•',
								icon: 'none'
							});
						} finally {
							uni.hideLoading();
						}
					},
					fail: (error) => {
						console.error('ÈÄâÊã©Â§¥ÂÉèÂ§±Ë¥•:', error);
						if (error.errMsg && error.errMsg.includes('cancel')) {
							// Áî®Êà∑ÂèñÊ∂àÈÄâÊã©
							return;
						}
						uni.showToast({
							title: 'ÈÄâÊã©Â§¥ÂÉèÂ§±Ë¥•',
							icon: 'none'
						});
					}
				});
			} catch (error) {
				console.error('Â§¥ÂÉèÈÄâÊã©Â§±Ë¥•:', error);
				uni.showToast({
					title: 'Â§¥ÂÉèÈÄâÊã©Â§±Ë¥•',
					icon: 'none'
				});
			}
		},
		
		// ‰∏ä‰º†Â§¥ÂÉèÂà∞‰∫ëÂ≠òÂÇ®
		async uploadAvatar(filePath) {
			try {
				console.log('ÂºÄÂßã‰∏ä‰º†Â§¥ÂÉè:', filePath);
				
				// ÁîüÊàêÂîØ‰∏ÄÁöÑÊñá‰ª∂Âêç
				const timestamp = Date.now();
				const random = Math.random().toString(36).substring(2, 11);
				const fileName = `avatar_${this.userInfo.userId}_${timestamp}_${random}.jpg`;
				
				// ‰∏ä‰º†Âà∞‰∫ëÂ≠òÂÇ®
				const uploadResult = await uniCloud.uploadFile({
					filePath: filePath,
					cloudPath: `avatars/${fileName}`,
					onUploadProgress: (progressEvent) => {
						console.log('‰∏ä‰º†ËøõÂ∫¶:', progressEvent);
					}
				});
				
				console.log('Â§¥ÂÉè‰∏ä‰º†ÊàêÂäü:', uploadResult);
				return uploadResult.fileID;
				
			} catch (error) {
				console.error('Â§¥ÂÉè‰∏ä‰º†Â§±Ë¥•:', error);
				throw error;
			}
		},
		
		// Â§ÑÁêÜÊòµÁß∞ÁÇπÂáª
		handleNicknameClick() {
			this.editNickname = this.userInfo.nickName || this.userInfo.username || '';
			this.$refs.nicknamePopup.open();
		},
		
		// ÂÖ≥Èó≠ÊòµÁß∞ÂºπÁ™ó
		closeNicknamePopup() {
			this.$refs.nicknamePopup.close();
		},
		
		// Á°ÆËÆ§ÊòµÁß∞‰øÆÊîπ
		async confirmNickname() {
			if (!this.editNickname.trim()) {
				uni.showToast({
					title: 'ÊòµÁß∞‰∏çËÉΩ‰∏∫Á©∫',
					icon: 'none'
				});
				return;
			}
			
			try {
				uni.showLoading({ title: '‰øùÂ≠ò‰∏≠...' });
				
				// Êõ¥Êñ∞ÊòµÁß∞
				this.userInfo.nickName = this.editNickname.trim();
				
				// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
				uni.setStorageSync('userInfo', this.userInfo);
				
				// Êõ¥Êñ∞Âà∞‰∫ëÊï∞ÊçÆÂ∫ì
				await this.updateUserInfo({ nickName: this.userInfo.nickName });
				
				this.closeNicknamePopup();
				
				uni.showToast({
					title: 'ÊòµÁß∞‰øùÂ≠òÊàêÂäü',
					icon: 'success'
				});
			} catch (error) {
				console.error('‰øùÂ≠òÊòµÁß∞Â§±Ë¥•:', error);
				uni.showToast({
					title: '‰øùÂ≠òÂ§±Ë¥•',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		},
		
		// Â§ÑÁêÜÊâãÊú∫Âè∑ÁÇπÂáª
		handlePhoneClick() {
			this.editPhone = this.userInfo.phone || '';
			this.smsCode = '';
			this.$refs.phonePopup.open();
		},
		
		// ÂÖ≥Èó≠ÊâãÊú∫Âè∑ÂºπÁ™ó
		closePhonePopup() {
			this.$refs.phonePopup.close();
		},
		
		// ÂèëÈÄÅÁü≠‰ø°È™åËØÅÁ†Å
		async sendSmsCode() {
			if (!this.editPhone) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑',
					icon: 'none'
				});
				return;
			}
			
			try {
				const result = await uniCloud.callFunction({
					name: 'send-sms-code',
					data: { phone: this.editPhone }
				});
				
				if (result.result.code === 0) {
					uni.showToast({
						title: 'È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅ',
						icon: 'success'
					});
					this.startSmsCountdown();
				} else {
					uni.showToast({
						title: result.result.message || 'ÂèëÈÄÅÂ§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				console.error('ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'ÂèëÈÄÅÂ§±Ë¥•',
					icon: 'none'
				});
			}
		},
		
		// ÂºÄÂßãÁü≠‰ø°ÂÄíËÆ°Êó∂
		startSmsCountdown() {
			this.smsCountdown = 60;
			const timer = setInterval(() => {
				this.smsCountdown--;
				if (this.smsCountdown <= 0) {
					clearInterval(timer);
				}
			}, 1000);
		},
		
		// Á°ÆËÆ§ÊâãÊú∫Âè∑ÁªëÂÆö
		async confirmPhone() {
			if (!this.editPhone) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑',
					icon: 'none'
				});
				return;
			}
			
			if (!this.smsCode) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•È™åËØÅÁ†Å',
					icon: 'none'
				});
				return;
			}
			
			try {
				uni.showLoading({ title: 'ÁªëÂÆö‰∏≠...' });
				
				// È™åËØÅÁü≠‰ø°È™åËØÅÁ†Å
				const result = await uniCloud.callFunction({
					name: 'verify-sms-code',
					data: {
						phone: this.editPhone,
						code: this.smsCode
					}
				});
				
				if (result.result.code === 0) {
					// Êõ¥Êñ∞ÊâãÊú∫Âè∑
					this.userInfo.phone = this.editPhone;
					
					// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
					uni.setStorageSync('userInfo', this.userInfo);
					
					// Êõ¥Êñ∞Âà∞‰∫ëÊï∞ÊçÆÂ∫ì
					await this.updateUserInfo({ phone: this.userInfo.phone });
					
					this.closePhonePopup();
					
					uni.showToast({
						title: 'ÊâãÊú∫Âè∑ÁªëÂÆöÊàêÂäü',
						icon: 'success'
					});
				} else {
					uni.showToast({
						title: result.result.message || 'È™åËØÅÁ†ÅÈîôËØØ',
						icon: 'none'
					});
				}
			} catch (error) {
				console.error('ÁªëÂÆöÊâãÊú∫Âè∑Â§±Ë¥•:', error);
				uni.showToast({
					title: 'ÁªëÂÆöÂ§±Ë¥•',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		},
		
		// Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØÂà∞‰∫ëÊï∞ÊçÆÂ∫ì
		async updateUserInfo(updateData) {
			try {
				const result = await uniCloud.callFunction({
					name: 'update-user-info',
					data: {
						userId: this.userInfo.userId,
						updateData: updateData
					}
				});
				
				console.log('Áî®Êà∑‰ø°ÊÅØÊõ¥Êñ∞ÊàêÂäü:', result);
			} catch (error) {
				console.error('Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error);
				throw error;
			}
		},
		
		// ÈÄÄÂá∫ÁôªÂΩï
		async handleLogout() {
			uni.showModal({
				title: 'Á°ÆËÆ§ÈÄÄÂá∫',
				content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
				success: async (res) => {
					if (res.confirm) {
						try {
							// Ë∞ÉÁî®ÈÄÄÂá∫ÁôªÂΩï‰∫ëÂáΩÊï∞
							await uniCloud.callFunction({
								name: 'logout',
								data: { token: uni.getStorageSync('token') }
							});
							
							// Ê∏ÖÈô§Êú¨Âú∞Â≠òÂÇ®
							uni.removeStorageSync('userInfo');
							uni.removeStorageSync('token');
							uni.removeStorageSync('loginType');
							uni.removeStorageSync('openid');
							uni.removeStorageSync('sessionKey');
							
							uni.showToast({
								title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
								icon: 'success'
							});
							
							// ËøîÂõû‰∏ä‰∏ÄÈ°µ
							setTimeout(() => {
								uni.navigateBack();
							}, 1500);
							
						} catch (error) {
							console.error('ÈÄÄÂá∫ÁôªÂΩïÂ§±Ë¥•:', error);
							// Âç≥‰Ωø‰∫ëÂáΩÊï∞Ë∞ÉÁî®Â§±Ë¥•Ôºå‰πüË¶ÅÊ∏ÖÈô§Êú¨Âú∞Êï∞ÊçÆ
							uni.removeStorageSync('userInfo');
							uni.removeStorageSync('token');
							uni.removeStorageSync('loginType');
							uni.removeStorageSync('openid');
							uni.removeStorageSync('sessionKey');
							
							uni.showToast({
								title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
								icon: 'success'
							});
							
							setTimeout(() => {
								uni.navigateBack();
							}, 1500);
						}
					}
				}
			});
		}
	}
}
</script>

